import React from 'react';

class App extends React.Component {
  constructor() {
    super()
    this.state = {
      message: "",
      author: ""
    }
  }

  componentDidMount(){
    const apiUrl = "https://type.fit/api/quotes";
    fetch(apiUrl)
      .then((response) => response.json())
      .then((data) => {
        let randomNumber = Math.floor(Math.random() * (data.length - 1))
        this.setState({
          message: data[randomNumber]["text"],
          author: data[randomNumber]["author"]
          })
        })
  }

  handleClick = () => {
    const apiUrl = "https://type.fit/api/quotes";
    fetch(apiUrl)
      .then((response) => response.json())
      .then((data) => {
        let randomNumber = Math.floor(Math.random() * (data.length - 1))
        this.setState({
          message: data[randomNumber]["text"],
          author: data[randomNumber]["author"]
          })
        })
      }

  copyToClipboard = () => {
    let copyQuote = this.state.message + " - " + this.state.author
    let textArea = document.createElement("textarea")
    textArea.value = copyQuote
    textArea.style.top = "0"
    textArea.style.left = "0"
    textArea.style.position = "fixed"
    document.getElementById("quote-box").appendChild(textArea)
    textArea.focus()
    textArea.select();
    document.execCommand("copy")
    textArea.remove()
  }

  render() {
    return (
      <div className="App">
        <div className="container-fluid p-2 bg-primary text-white border rounded-top">
          <h1 className="text-center">Random Quote Machine</h1>
        </div>
        <div id="quote-box" className="container mt-5 col-lg-7 mt-3">
          <blockquote className="blockquote text-right">
            <h2 id="text" className="quote-text"><q>{this.state.message}</q></h2>
          <h3 id="author" className="blockquote-footer">{this.state.author}</h3>
          </blockquote>
          <div className="d-flex justify-content-between">
            <div>
              <a href={`https://twitter.com/intent/tweet?text=${this.state.message}${this.state.author}`} target="_blank" rel="noopener noreferrer" id="tweet-quote" className="btn btn-primary ml-1" role="button"><i className="fab fa-twitter-square"></i></a>
              <button type="button" className="btn btn-primary ml-1" onClick={this.copyToClipboard}>Copy this quote</button>
            </div>
            <div>
              <button type="button" id="new-quote" className="btn btn-success" onClick={this.handleClick}>New quote</button>
            </div>
          </div>
        </div>
        <div style={{height: 45, backgroundColor: "white"}}></div>
        <div style={{height: 45, backgroundColor: "white"}} className="fixed-bottom d-flex justify-content-center align-items-end">
          <p className="text-center font-italic small">Made by Dan Sprucek as a part of freeCodeCamp certification</p>
        </div>
      </div>
    );
  }
}
export default App;